import React from "react";
import postMessageToParent from "./postMessageToParent";
import "./styles.css";

export default function Card() {
  const handleChange = (change) => {
    const { value } = change.target;

    postMessageToParent({
      from: "card",
      messageType: "validation",
      data: {
        valid: /^[0-9]{13,19}$/.test(value),
      },
    });
  };

  const handleFocus = () => {
    postMessageToParent({
      from: "card",
      messageType: "event",
      data: {
        eventName: "onfocus",
        value: true,
      },
    });
  };

  const handleBlur = () => {
    postMessageToParent({
      from: "card",
      messageType: "event",
      data: {
        eventName: "onblur",
        value: true,
      },
    });
  };

  return (
    <input
      type="text"
      name="card"
      placeholder="13 to 19 digits"
      onChange={handleChange}
      onFocus={handleFocus}
      onBlur={handleBlur}
    />
  );
}
